import { Card } from "../Card/Card";
import { OverflowScrollabe } from "../OverflowScrollable/OverflowScrollabe";

import { useTransactions } from "../../hooks/useTransactions";

import {
    CardContainer,
    DashboardContainer,
} from "./DashboardStyles";

import iconIncome from "../../assets/income.svg";
import iconOutcome from "../../assets/outcome.svg";
import iconTotal from "../../assets/total.svg";

export const Dashboard = () => {
    const { transactions } = useTransactions();

    const sumary = transactions?.reduce((acc, transaction) => {
        if (transaction.type === "deposit") {
            acc.deposit = acc.deposit + transaction.amount;
        } else {
            acc.withdrawal = acc.withdrawal - transaction.amount;
        }

        acc.total = acc.deposit - acc.withdrawal;
        
        return acc;
    }, {
        deposit: 0,
        total: 0,
        withdrawal: 0
    });

    return (
        <DashboardContainer>
            <OverflowScrollabe
                borderRadius={5}
                buttonSize={48}
                scrollAmount={1}
            >
                <CardContainer>
                    <Card
                        background="--shape-principal"
                        color="--titulos"
                        icon={iconIncome}
                        name="Entradas"
                        value={sumary?.deposit ?? 0}
                    />
                    <Card
                        background="--shape-principal"
                        color="--titulos"
                        icon={iconOutcome}
                        name="Saídas"
                        value={sumary?.withdrawal ?? 0}
                    />
                    <Card
                        background="--green"
                        color="--shape-principal"
                        icon={iconTotal}
                        name="Total"
                        value={sumary?.total ?? 0}
                    />
                </CardContainer>
            </OverflowScrollabe>
        </DashboardContainer>
    );
}