import { valueToCurrencyNoSymbol } from "../../utils/currencyFormater";

import { CardContainer } from "./CardStyles";

interface CardProps {
    background: string;
    color: string;
    icon: string;
    name: string;
    value: number;
};

export const Card = ({
    background,
    color,
    icon,
    name,
    value
}: CardProps) => {
    return (
        <CardContainer
            background={background}
            color={color}
        >
            <img
                alt={name}
                src={icon}
            />

            <h2>{name}</h2>

            <strong>
                <span>{value < 0 ? "- R$" : "RS"}</span> {valueToCurrencyNoSymbol(value)}
            </strong>

            <span className="mobile-span">Última entrada dia 13 de abril</span>
        </CardContainer>
    );
}